import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Erasure(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/erasure.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[5.85, 1.98, -0.71]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere180.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere180_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere180_2.geometry}
          material={materials.outline}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere180_3.geometry}
          material={materials["glass.005"]}
        />
      </group>
    </group>
  );
}
export default Erasure;
useGLTF.preload("/glossarySelected/erasure.glb");
